.navbar{
    background-image:  url(../Images/Toit-Pune-brewpub-10\ 1\ \(1\).png) ;
    background-color: linear-gradient(180deg, rgb(0, 0, 0), rgb(0, 0, 0));
    height: auto;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    /* z-index: -100.1; */
    position: relative;
    margin-top: -2px;
}


.navbar:after {
	content: '';
	display: block;
	position: absolute;
	left: 0px;
	right: 0;
	top: 0;
	bottom: 0;
	background: rgba(0, 0, 0, 0.005);
	background-image: radial-gradient(rgb(0, 0, 0) -10%, transparent 20%);
	background-size: 20px 20px;
    z-index: -100.1;
}

.beer{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 8rem;
    font-size: 10px;
    
}





