.foter-container {
  display: flex;
  flex-direction: column;
  background-color: black;
  width: 100%;
  height: 45rem;
  position: relative;
  flex: 3;
}

.footer-1 {
  color: white;
  flex: 1;
  list-style: none;align-items: center;
  justify-content: center;
  position: absolute;
  top: 100px;
  left: 100px;
}



.footer-1 li{
    margin: 40px;
    color: #DC9696;
    display: block;
}

.footer-2{
    flex: 1;
}

.footer-2 img{
    position: absolute;
    left: 595px;
    transform: scale(0.8);
    top: 30px;

}

.footer-2 h1{
    color: white;
    position: absolute;
    top: 140px;
    left: 600px;
    font-size: 20px;
    font-weight: lighter;
}

.footer-3{
    flex: 1;
    position: absolute;
    top: 100px;
    left: 920px;
}

.footer-3 li{
    margin: 40px;
    color: #DC9696;
    display: block;
}

.footerglass{
    transform: scale(0.7);
    margin-top: -456px;
    margin-left: 250px;
    height: 100px;
}


